$darkest-blue: #26295F;
$darker-blue: #353983;
$dark-blue: #4449A7;
$light-blue: #4876E6;
$lighter-blue: #6065BE;
$lightest-blue: #6AA6EF;
$purple-highlight: #40265F;
$purple-highlight-light: #716AEF;
$teal-highlight: #26465F;
$teal-highlight-light: #447BA7;
$teal-highlight-lighter: #48C5E6;

body {
    background: $darkest-blue;
    color: white;
    font-family: "Libre Baskerville", serif;
    font-weight: 400;
    font-style: normal;
}

#topbar {
    background-color: #002644;
    border: solid $purple-highlight;
    display: flex;
    justify-content: space-between;
    height: 100px;
    border-radius: 15px;
    opacity: 90%;
}

#topbar > img {
    object-fit: contain;
    margin-left: 30px;
}

.navbar {
    list-style: none;
    width: 150px;
    display: flex;
    flex: 1;
    justify-content: end;
    align-items: center;
    gap: 45px;
    padding-right: 50px;
}

.navbar > a {
    color: $lighter-blue;
    align-self: center;
    /*
    Text sizing
    */
}

#landing {
    height: calc(100svh - 180px);
    display: grid;
    grid-template-columns: 1fr 600px 1fr;
    grid-template-rows: auto;
    align-content: center;
    }

#landing > * {
    justify-self: center;
}

#landing > h1 {
    grid-column: 2;
    grid-row: 1;
    font-size: 5em;
}

#landing > h2 {
    grid-column: 2;
    grid-row: 2;
}

#landing > p {
    grid-column: 2;
    grid-row: 3;
    font-size: .75em;
}

.center-heading {
    text-decoration: underline;
    // padding: 5px 100px 5px 100px;
    width: 95%;
    padding: 5px 0px 5px 0px;
    text-align: center;
    border: solid $darker-blue;
    background-color: lightgray;
    border-radius: 10px;
    opacity: 90%;
    color: $darker-blue;
}

#workhistory {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

#workhistory > div {
    width: 97%;
    border: solid $light-blue;
    border-radius: 5px;
    margin: 10px;
    padding-left: 10px;
    background-color: $purple-highlight-light;
    opacity: 60%;
}

#workhistory h1 {
    color: $darker-blue;
}

#workhistory h2 {
    color: $lightest-blue;
}

#workhistory h3 {
    font-family: serif;
    font-style: italic;
    color: $lighter-blue;
}

#workhistory p {
    font-style: italic;
}

.listing {
    padding-left: 200px;
    padding-bottom: 10px
}

.listing > * {
    margin: 5px auto;
}

.jobhighlights {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

#skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

.no-list {
    list-style: none;
}

.skillsgroup {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.skillgroup {
    border: solid indianred;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.skillgroup > h1 {
    width: 100%;
    height: 2em;
    border: solid peachpuff;
    padding-left: 10px;
}

.skillgroup ul {
    border: solid cornflowerblue;
    text-align: center;
    list-style-position: inside;
    padding: 5px;
}

.concept {
    font-style: italic;
}

.technology {
    font-weight: bold;
}

#contact {
    text-align: center;
}

.emphasis {
    color: $teal-highlight-lighter;
}